
.grid-item {
  padding-bottom: 30px;
  padding-left: 15px !important; 
  padding-right:15px !important;
}
@media only screen and (min-width: 320px) and (max-width: 599px){
.grid-item {
    width: 100%;
}
}
@media only screen and (min-width: 600px) {
.grid-item {
    width: 50%;
}
}
.half {
  width: 50%;
}
.full {
  width: 100%;
}
.main {
  margin-top: 25px;
}
.post-add {
  position: fixed;
  bottom: 40px;
  right: 40px;
  /*margin-bottom:30px;*/
}



